.breed-select {
  width: 200px;
  margin: 2%;
}

p.loader {
  width: 54px;
  height: 54px;
  background-color: #fff;
  background-image: radial-gradient(5px, #ff3d00 100%, rgba(0, 0, 0, 0) 0), radial-gradient(5px, #ff3d00 100%, rgba(0, 0, 0, 0) 0), radial-gradient(5px, #ff3d00 100%, rgba(0, 0, 0, 0) 0), radial-gradient(5px, #ff3d00 100%, rgba(0, 0, 0, 0) 0), radial-gradient(5px, #ff3d00 100%, rgba(0, 0, 0, 0) 0), radial-gradient(5px, #ff3d00 100%, rgba(0, 0, 0, 0) 0);
  background-repeat: no-repeat;
  border-radius: 4px;
  animation: move 4s linear infinite, rotate 2s linear infinite;
  display: none;
  position: relative;
}

@keyframes rotate {
  0%, 20% {
    transform: rotate(0deg);
  }

  30%, 40% {
    transform: rotate(90deg);
  }

  50%, 60% {
    transform: rotate(180deg);
  }

  70%, 80% {
    transform: rotate(270deg);
  }

  90%, 100% {
    transform: rotate(360deg);
  }
}

@keyframes move {
  0%, 9% {
    background-position: -12px -15px, -12px 0, -12px 15px, 12px -15px, 12px 0, 12px 15px;
  }

  10%, 25% {
    background-position: 0 -15px, -12px 0, -12px 15px, 34px -15px, 12px 0, 12px 15px;
  }

  30%, 45% {
    background-position: 0 -34px, -12px -10px, -12px 12px, 34px -15px, 12px -10px, 12px 12px;
  }

  50%, 65% {
    background-position: 0 -34px, -12px -34px, -12px 12px, 34px -12px, 0 -10px, 12px 12px;
  }

  70%, 85% {
    background-position: 0 -34px, -12px -34px, 0 12px, 34px -12px, 0 -10px, 34px 12px;
  }

  90%, 100% {
    background-position: 0 -34px, -12px -34px, 0 0, 34px -12px, 0 0, 34px 12px;
  }
}

.is-hidden, .error {
  display: none;
}

